	data() {
		return {
			challenge_list: [{
				endtime: '2天6小时',
				hotnum: '2.3w'
			}, {
				endtime: '2天6小时',
				hotnum: '2.3w'
			}]
		}
	},
	onPullDownRefresh: function() {
		console.log('onPullDownRefresh');
	},
	onLoad() {
		this.current = this.current;
	},
	methods: {
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		target(tag) {
			if (tag == '') {
				return;
			}
			uni.navigateTo({
				url: '../../template/' + tag + '/' + tag
			});
		},
		open() {
			uni.request({
				url: 'https://www.icoo.tech/wx/test',
				method: 'GET',
				data: {},
				success: res => {
					console.log(res)
				},
				fail: (res) => {
					console.log('err:%s', res)
				},
				complete: () => {}
			});
		}
	}
}



